<template>
    <v-container class="py-10" fluid>
        <v-row justify="center">
            <v-col cols="9">
                <v-alert
                    v-model="alert"
                    :type="alertType"
                    border="left"
                    close-text="Close Alert"
                    elevation="10"
                    dismissible
                    transition="scale-transition"
                    style="
                        position: fixed;
                        top: 20%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        z-index: 9999;
                        max-width: 35rem;
                    "
                >
                    <v-row align="center">
                        <v-col class="grow">
                            {{ alertMessage }}
                        </v-col>
                        <v-col class="shrink">
                            <v-btn v-if="imageUsed" to="/terminals/slideshows"
                                >Go to slideshow</v-btn
                            >
                        </v-col>
                    </v-row>
                </v-alert>
                <v-card class="rounded-lg pb-2">
                    <v-toolbar color="green" dark flat>
                        <v-toolbar-title>Media Library</v-toolbar-title>
                    </v-toolbar>
                    <v-card-title class="justify-center pt-2 pb-0 black--text">
                        Upload an Image
                    </v-card-title>
                    <v-form
                        ref="uploadForm"
                        v-model="uploadValid"
                        lazy-validation
                    >
                        <v-file-input
                            class="mx-10"
                            chips
                            counter
                            show-size
                            truncate-length="50"
                            accept="image/*"
                            label="Choose File"
                            v-model="originalFile"
                            hint="Recommended dimensions: 840 x 480"
                            persistent-hint
                        />
                    </v-form>

                    <v-btn
                        right
                        @click="uploadMedia"
                        :disabled="!uploadValid"
                        class="justify-center"
                        style="display: block; margin: 0 auto 1rem"
                    >
                        Upload
                    </v-btn>

                    <v-card-subtitle class="pt-0 pb-2 text-center">
                        Upload png, jpg, gif, mpeg, or QuickTime video.
                    </v-card-subtitle>
                    <v-row class="mt-6">
                        <v-col
                            :lg="3"
                            :md="4"
                            :sm="6"
                            :xs="12"
                            v-for="{ thumbnailUrl, id, fileUrl } in mediaData"
                            :key="id"
                            class="d-flex flex-column justify-center align-center"
                        >
                            <v-card class="mb-4">
                                <button
                                    @click="
                                        showImage(fileUrl), (showModal = true)
                                    "
                                >
                                    <v-img :src="thumbnailUrl" />
                                </button>
                            </v-card>

                            <div>
                                <v-btn
                                    color="error"
                                    @click="deleteMediaAsset(id)"
                                    >Remove</v-btn
                                >
                            </div>
                        </v-col>
                        <v-col v-if="notFound">
                            <template>
                                <v-alert
                                    prominent
                                    text
                                    type="info"
                                    class="ma-2"
                                >
                                    <v-row align="center">
                                        <v-col class="grow">
                                            No records found</v-col
                                        >
                                        <v-col class="shrink">
                                            <v-btn
                                                color="green"
                                                class="my-2 white--text"
                                                @click="initialize"
                                            >
                                                Retry
                                            </v-btn>
                                        </v-col>
                                    </v-row>
                                </v-alert>
                            </template>
                        </v-col>
                        <v-col
                            v-if="libraryEmpty"
                            class="d-flex flex-column align-center"
                        >
                            <div class="mb-6">
                                <img
                                    src="../../assets/images/263142.png"
                                    alt="screenshot"
                                    style="max-width: 15rem"
                                />
                            </div>
                            <p>
                                Upload or click on an image in the Image Library
                                to add slides!
                            </p>
                        </v-col>
                    </v-row>
                    <v-pagination
                        class="mt-6"
                        v-model="page"
                        circle
                        :length="pagesTotal"
                        :total-visible="7"
                        @input="getMediaLibrary"
                    ></v-pagination>
                </v-card>
            </v-col>
        </v-row>
        <modal
            v-if="showModal"
            @close="showModal = false"
            style="
                position: fixed;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 9999;
                max-width: 35rem;
            "
        >
            <v-card style="position: relative; padding: 1rem">
                <div>
                    <button
                        @click="showModal = false"
                        style="position: absolute; top: 4px; right: 4px"
                    >
                        X
                    </button>
                </div>
                <div><v-img :src="imageSource" /></div>
            </v-card>
        </modal>
    </v-container>
</template>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';

export default {
    name: 'MediaLibraryScreen',
    data: () => ({
        loading: false,
        valid: false,
        alert: false,
        alertMessage: '',
        alertType: 'success',
        imageUsed: false,
        pagesTotal: '',
        showModal: false,
        imageSource: '',
        mediaData: [],
        libraryEmpty: false,
        page: 1,
        originalFile: null,
        uploadValid: false,
    }),
    mounted() {
        this.initialize();
    },
    computed: {
        ...mapGetters(['getAppColors', 'getInternalCoreClearController']),
    },
    methods: {
        initialize() {
            this.getMediaLibrary()
        },
        async getMediaLibrary(input) {
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController + '/get_media'}`,
                    {
                        params: { page: input },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );

                if (response.data.results) {
                    console.log(response);
                    this.pagesTotal = response.data.pages;
                    const mediaLibrary = response.data.results;
                    this.mediaData.length = 0;
                    for (let i = 0; i < mediaLibrary.length; i++) {
                        let id = mediaLibrary[i].id;
                        let thumbnailUrl = mediaLibrary[i].thumbnailUrl;
                        let fileUrl = mediaLibrary[i].fileUrl;
                        this.mediaData.push({
                            id,
                            thumbnailUrl,
                            fileUrl,
                        });
                    }
                } else {
                    this.mediaData = [];
                    console.log('Media library not found!');
                    this.alert = true;
                    this.alertType = 'info';
                    this.alertMessage = 'No records found';
                }
            } catch (error) {
                console.error('Could not get media library');
                console.error(error);
                this.alert = true;
                this.alertType = 'error';
                this.alertMessage = error;
            }
        },

        async uploadMedia() {
            console.log(this.originalFile);
            let formData = new FormData();
            formData.append(
                'upload_media',
                this.originalFile,
                this.originalFile.name,
            );

            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController + '/upload_media'}`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    },
                );
                let getResponse = await axios.get(
                    `${this.getInternalCoreClearController + '/get_media'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                console.log(response);
                if (response.data) {
                    const lastPage = getResponse.data.pages;
                    this.getMediaLibrary(lastPage);
                    this.page = lastPage;
                } else {
                    console.log('Media library not found!');
                }
            } catch (error) {
                console.error('Could not upload media');
                console.error(error);
            }
            this.reset();
        },
        async deleteMediaAsset(id) {
            try {
                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController +
                        '/delete_media_asset'
                    }`,
                    {
                        params: { media_id: id },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    console.log('deleted');
                    this.imageUsed = false;
                    this.alert = true;
                    this.alertType = 'success';
                    this.alertMessage = 'Asset deleted';
                    setTimeout(() => {
                        location.reload();
                    }, 2000);
                } else {
                    const imageMessage = response.data.responseDescription;
                    console.log(response.data);
                    this.alert = true;
                    this.alertType = 'error';
                    this.alertMessage = imageMessage;
                    this.imageUsed = true;
                }
            } catch (error) {
                console.error('Could not get media library');
                console.error(error);
            }
        },
        showImage(fileUrl) {
            this.imageSource = fileUrl;
        },
        reset() {
            this.$refs.uploadForm.reset();
        },
    },
};
</script>

<style scoped></style>
